@import './utils/animations';
@import './utils/mixins';

body {
  margin: 0;
  height: 100vh;

  @include items-center;
}

.stopwatch {
  position: relative;
  height: 80vmin;
  width: 80vmin;
  border-radius: 50%;
  border: 1vmin dotted #000;

  &__minutes {
    margin: auto;
    height: 20vmin;
    width: 3vmin;
    position: absolute;
    inset: 0 0 20vmin;
    background-color: #0700ff;
    transform-origin: bottom center;
    animation: rotation 3600s steps(60) infinite;
  }

  &__seconds {
    margin: auto;
    height: 38vmin;
    width: 1.5vmin;
    position: absolute;
    inset: 0 0 38vmin;
    background-color: #2c8000;
    animation: rotation 60s linear infinite;
    transform-origin: bottom center;
  }

  &__center {
    margin: auto;
    height: 5vmin;
    width: 5vmin;
    position: absolute;
    inset: 0;
    background-color: #f6a603;
    border-radius: 50%;
  }
}

.stopwatch--speed-up {
  .stopwatch__minutes {
    animation: rotation 600s steps(60) infinite;
  }

  .stopwatch__seconds {
    animation: rotation 10s linear infinite;
  }
}
